<!--  -->
<template>
  <div class='header'>
    <div>
      一、安装 less 和less-loader <br>
      # npm install less less-loader --save<br><br>
      二、查看版本 <br>
      # lessc -v <br>
      版本 4.1.1 要修改版本<br><br>
      三、在package.json配置文件中加入以下内容：<br>
      “less”: “^3.9.0”,<br>
      “less-loader”: “^5.0.0”,<br><br>
      四、.重新安装less<br>
      # npm i <br><br>
      五、在webpack.base.config.js的module rules中添加一下内容：<br>
      {<br>
      test: /\.less$/,<br>
      loader: "style-loader!css-loader!less-loader",<br>
      options: { sourceMap: true }<br>
      },<br><br>
      六、<br>
      <img src="../../assets/2.png" alt="">


    </div>
    <div>
      <navs></navs>
    </div>
  </div>
</template>

<script>
  import navs from './navs.vue';
  export default {
    components: {
      navs
    },
    name: '',
    data() {
      //这里存放数据
      return {

      };
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {

    },
    //方法集合
    methods: {

    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {

    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //beforeCreate() {}, //生命周期 - 创建之前
    //beforeMount() {}, //生命周期 - 挂载之前
    //beforeUpdate() {}, //生命周期 - 更新之前
    //updated() {}, //生命周期 - 更新之后
    //beforeDestroy() {}, //生命周期 - 销毁之前
    //destroyed() {}, //生命周期 - 销毁完成
    //activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
  }

</script>
<style lang='less' scoped>
  .header {
    margin-bottom: 60px;
  }

</style>



